<template>
  <div class="file-upload" :class="{'theme--dark': $vuetify.theme.isDark}">

    <div
      class="file-upload__wrap"
      v-if="!image"
      :class="{'image-dropping': dragging}"
      @dragover="dragging = true"
      @dragleave="dragging = false"
      >
      <input class="file-upload__input" type='file' accept="image/*" @change="readURL" />
      <div class="drag-text">
        <v-icon large>{{ icons.mdiFileUpload }}</v-icon>
        <div class="my-4"></div>
        <p>Rasim qo’shish</p>
      </div>
    </div>

    <div class="file-upload__content" v-else>
      <img class="file-upload__image" :src="image " alt="your image" />
      <v-btn
        depressed
        color="error w-full"
        @click="handleRemove"
      >
        Remove
      </v-btn>
    </div>

  </div>
</template>

<script>
import { mdiFileUpload } from '@mdi/js';


export default {
  name: "ImageUpload",

  data() {
    return {
      image: null,
      dragging: false,

      icons: {
        mdiFileUpload
      }
    }
  },

  methods: {
    readURL(e) {
      const input = e.target;

      if (input.files && input.files[0]) {
        const reader = new FileReader();

        reader.readAsDataURL(input.files[0]);

        reader.onload = (e) => {
          this.image = e.target.result;
        };
      }
      // else {
      //   removeUpload(input);
      // }
    },

    handleRemove() {
      this.image = null;
    }
  },

  mounted() {
    console.log(this.$vuetify);
  }
}
</script>

